.App {
  text-align: center;
}

.container-section{
  height: 80vh;
  width: 100vw;
}

#hero,#about,#project,#contact{
  display: flex;
  justify-content: center;
  align-items: center;
}

#project ul,#footer ul{
  list-style: none;
  display: flex;
  flex-direction: column;
}

#project ul li{
  margin: 1em;
}

.custom-row label{
  width: 25%;
  display: inline !important;
  padding-right: 1em;
  text-align: center;
  vertical-align: middle;
}

.custom-row .form-control{
  width:50%;
  display: inline !important;
  margin: 1em 2em;
  padding: 1em 2em;
}

#footer {
  list-style: none;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
